import React from 'react';
import { PrimaryButton } from '@fluentui/react';
import { fetchCharacters, saveCharacterToSharePoint } from './Api/Api';

const LoadSaveToListButton: React.FC = () => {

    const loadAndSaveCharacters = async () => {
        try {
            const characters = await fetchCharacters();
            const savePromises = characters.map(saveCharacterToSharePoint);
            await Promise.all(savePromises);

            alert('Vetky postavy boli spene uloen do SharePointu.');
        } catch (error) {
            console.error('Chyba pri ukladan postv do SharePointu:', error);
            alert('Nepodarilo sa uloi postavy do SharePointu.');
        }
    };

    return (
        <div>
            <PrimaryButton onClick={loadAndSaveCharacters}>Nata z API</PrimaryButton>
        </div>
    );
};

export default LoadSaveToListButton;